@import '../../App.scss';

//form 
.form__wrapper {
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 100%;
   margin: 139px 0 166px 0;
}

.Form {
   display: flex;
   flex-direction: column;
   width: 470px;

   input {
      height: 38px;
      margin-bottom: 18px;
   }

   .checkBox {
      margin-bottom: 12px;

      label {
         margin: 0;
         padding-left: 8px;
      }
   }

   label,
   h6 {
      font-size: 16px;
      color: $textBlack;
      margin-bottom: 9px;
   }

   .btn_formSubmit {
      margin: 0 auto;
   }

   div:nth-of-type(3) {
      margin-bottom: 22px
   }

   .inputPhoto {
      margin-bottom: 38px
   }
}

.textErrorForm{
   display: flex;
   justify-content: center;
   font-size: 16px;
   font-family: $font;
   color: #f44336;
}

//Radio input
@supports(-webkit-appearance: none) or (-moz-appearance: none) {

   input[type='checkbox'],
   input[type='radio'] {
      --active: #007bff;
      --active-inner: #fff;
      --focus: 2px rgba(39, 94, 254, .3);
      --border: #BBC1E1;
      --border-hover: #007bff;
      --background: #fff;
      --disabled: #F6F8FF;
      --disabled-inner: #E1E6F9;
      -webkit-appearance: none;
      -moz-appearance: none;
      height: 21px;
      outline: none;
      display: inline-block;
      vertical-align: top;
      position: relative;
      margin: 0;
      cursor: pointer;
      border: 1px solid var(--bc, var(--border));
      background: var(--b, var(--background));
      transition: background .3s, border-color .3s, box-shadow .2s;

      &:after {
         content: '';
         display: block;
         left: 0;
         top: 0;
         position: absolute;
         transition: transform var(--d-t, .3s) var(--d-t-e, ease), opacity var(--d-o, .2s);
      }

      &:checked {
         --b: var(--active);
         --bc: var(--active);
         --d-o: .3s;
         --d-t: .6s;
         --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
      }

      &:disabled {
         --b: var(--disabled);
         cursor: not-allowed;
         opacity: .9;

         &:checked {
            --b: var(--disabled-inner);
            --bc: var(--border);
         }

         &+label {
            cursor: not-allowed;
         }
      }

      &:hover {
         &:not(:checked) {
            &:not(:disabled) {
               --bc: var(--border-hover);
            }
         }
      }

      &:focus {
         box-shadow: 0 0 0 var(--focus);
      }

      &:not(.switch) {
         width: 21px;

         &:after {
            opacity: var(--o, 0);
         }

         &:checked {
            --o: 1;
         }
      }

      &+label {
         font-size: 14px;
         line-height: 21px;
         display: inline-block;
         vertical-align: top;
         cursor: pointer;
         margin-left: 4px;
      }
   }

   input[type='checkbox'] {
      &:not(.switch) {
         border-radius: 7px;

         &:after {
            width: 5px;
            height: 9px;
            border: 2px solid var(--active-inner);
            border-top: 0;
            border-left: 0;
            left: 7px;
            top: 4px;
            transform: rotate(var(--r, 20deg));
         }

         &:checked {
            --r: 43deg;
         }
      }

      &.switch {
         width: 38px;
         border-radius: 11px;

         &:after {
            left: 2px;
            top: 2px;
            border-radius: 50%;
            width: 15px;
            height: 15px;
            background: var(--ab, var(--border));
            transform: translateX(var(--x, 0));
         }

         &:checked {
            --ab: var(--active-inner);
            --x: 17px;
         }

         &:disabled {
            &:not(:checked) {
               &:after {
                  opacity: .6;
               }
            }
         }
      }
   }

   input[type='radio'] {
      border-radius: 50%;

      &:after {
         width: 19px;
         height: 19px;
         border-radius: 50%;
         background: var(--active-inner);
         opacity: 0;
         transform: scale(var(--s, .7));
      }

      &:checked {
         --s: .5;
      }
   }
}


//Form media
@media screen and (max-width: 768px) {
   .form__wrapper {
      margin: 6px 0 119px 0;
      padding-left: 9px;
   }

   .Form {
      div:nth-of-type(3) {
         margin-bottom: 21px
      }
   }

   footer {
      padding-left: 10px;
   }
}

@media screen and (max-width: 360px) {
   .Form {
      width: 100%;
      margin-bottom: 83px;
   }

   .form__wrapper {
      padding: 0;
      margin: 0;
   }

   .btn {
      margin-bottom: 60px;
   }

   footer {
      padding-left: 0px;
   }

}
